:root{
  --main-color: #ffffff;
  --main-bg-color: #EF9711;
}

.App {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  width: 100%;
}
.slider-track{
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  width: 124vh;
  overflow: hidden;
}

.slider-btn{
  z-index: 1;
  height: 10em;
  width: 0;
}
.slider-track:hover .slider-btn{
  box-shadow: 20px -12px 40px 80px #ffffff;
  border: none;
  z-index: 4;
}

.slider-btn button{
  border-radius: 50px;
  z-index: 1;
  border: 1px #ece8e6 solid;
  height: 40px;
  width: 40px;
  padding: 0.6em;
  color: #868686;
  background: #ffffff;
}
.slider-track #prev{
  transition: visibility 0.5s;
}
.slider-track #next{
  position: absolute;
  transition: visibility 0.5s;
  margin-left: 113vh;
}
.card-show-container{
  background-color: var(--main-bg-color);
  height: 10vh;
  width: 100%;
}
.card-show-wrapper{
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  color: var(--main-color);
}
.card-show-wrapper i{
  order: 1;
  font-size: 30px;
}
.card-show-wrapper h2{
  order: 3;
  font-size: 30px;
}
.card-show-wrapper h4{
  order: 2;
  font-size: 20px;
}
.hidden{
  visibility: hidden;
}






