button{
    border: none;
    background-color: rgba(255,255,255,0.01);

}

.card-btn{
    padding: 0.5em;
    border-radius: 5px;
    width: 80%;
    background-color: var(--main-bg-color);
}
.card-btn button{
    color: var(--main-color);
}
.card-btn:hover{
    background-color: #D17A29;
    transition: background-color 0.6s;
}
.card-btn span{
    margin-left: 10px;
}
